@import 'variables';

//Desktop
@media screen and (min-width: $breakpoint-dk-min) {
  #project-container {
    margin: 1.5rem;
    position: relative;
    width: 50em;
    // display: flex;
    // flex-direction: row;

    .prjt-img {
      width: 50em;
      height: 50em;
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 50em;
      height: 50em;
      background: rgba(0, 0, 0, 0.6);
      font-family: inherit;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transition: opacity 0.25s;
      margin-bottom: 1em;

      .overlay_p {
        font-size: 1.75em;
        margin: 1em 2em;
      }
    }
    .overlay:hover {
      backdrop-filter: blur(5px);
      opacity: 1;
    }

    .overlay_title {
      font-size: 2.5em;
      text-decoration: underline;
      font-weight: bold;
      display: flex;
      justify-content: center;
      margin-bottom: 0.1em;
    }

    .project_descriptors {
      display: flex;
      align-items: center;
      flex-flow: row;
      justify-content: space-evenly;

      .overlay_btn {
        color: white;
        text-decoration: none;
        font-size: 1.25em;
      }
    }
  }
}

// mobile
@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  .content-title {
    margin-top: 5rem;
    display: flex;
    justify-content: center;
  }

  #project-container {
    margin: 1.5rem;
    position: relative;
    width: 22.5em;
    padding-bottom: 1em;

    .prjt-img {
      width: 100%;
      height: 22.5em;
    }

    .overlay_title {
      font-size: 1.5em;
      display: flex;
      justify-content: center;
      text-decoration: underline;
      margin-bottom: 0.5em;
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 80%;
      background: rgba(0, 0, 0, 0.6);
      font-family: inherit;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transition: opacity 0.25s;

      .overlay_p {
        font-size: 0.95em;
        margin-top: 0.25em;
        margin-left: 0.55em;
      }
    }
    .overlay:hover {
      backdrop-filter: blur(5px);
      opacity: 1;
    }
  }

  .project_descriptors {
    display: flex;
    align-items: center;
    flex-flow: row;
    justify-content: space-evenly;

    .overlay_btn {
      color: white;
      text-decoration: none;
      font-size: 1.1em;
    }
  }
}
