@import 'variables';

//Desktop
@media screen and (min-width: $breakpoint-dk-min) {
  // body {
  //   overflow: hidden;
  // }
  .mainpg-container {
    margin: 3em 0 5em 0;
    font-size: 1.75em;
    #profile-container {
      padding: 0.25em;
      margin: 0 1.75em;
      box-shadow: 0.5px 1px 10px black;
      max-width: 65em;
      .descriptors {
        margin: 2.5rem;
        display: flex;
        align-items: center;
        flex-direction: column;

        p {
          margin: 2em;
          max-width: inherit;
        }

        .description {
          font-style: italic;
          font-size: 0.75em;
        }

        button {
          border: none;
          margin-top: 6em;
          background-color: transparent;
          color: white;
        }
        button:hover {
          cursor: pointer;
        }
      }
    }
  }

  #refh {
    display: flex;
    justify-content: center;
    font-size: 5.5em;
  }

  #prj-section {
    display: flex;
    margin-left: 7.5em;
    padding-bottom: 1em;
    flex-flow: row;
  }
}

// mobile display
@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  .contact-btn {
    font-size: 1.5rem;
    // position: relative;
    // top: 0.2rem;
    // right: 0.5rem;
    margin: 0.5rem 0 0 16.75rem;
    background-color: transparent;
    border: none;
    color: white;
  }

  #refh {
    display: flex;
    justify-content: center;
  }

  .mainpg-container {
    font-size: 1.25rem;

    #profile-container {
      // display: flex;
      // align-items: center;
      margin: 1.25rem;
      box-shadow: 0.5px 1px 10px black;

      .descriptors {
        margin: 2.5rem;
        display: flex;
        align-items: center;
        flex-direction: column;

        .name {
          margin-bottom: 0;
        }

        p {
          margin: 0.5rem;
          font-size: 1rem;
        }

        .description {
          font-style: italic;
        }

        button {
          display: none;
        }
      }
    }
  }
}

// tablet display
@media (min-width: $breakpoint-t-min) and (max-width: $breakpoint-t-max) {
  #profile-container {
    font-size: 2rem;
    margin: 3rem;
    background-size: cover;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 1rem;
    .description {
      max-width: fit-content;
    }
  }
}
