@import 'variables';

//Desktop
@media screen and (min-width: $breakpoint-dk-min) {
  #contact-container {
    margin: 5em;
    display: flex;
    flex-flow: column;
    align-items: center;

    h1 {
      font-size: 2em;
      margin-bottom: 1em;
    }
  }
}

@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  #contact-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    h1 {
      margin-top: 1em;
    }
    color: white;
    iframe {
      width: 100%;
    }
  }
}
