@import 'variables';

//Desktop
@media screen and (min-width: $breakpoint-dk-min) {
  .navbar {
    margin: 1.5em 0;
    // border-bottom: 1px solid black;
    // box-shadow: 2px 5px 5px black;
    font-size: 2.4vh;
    ul {
      display: flex;
      justify-content: end;
      li {
        margin-right: 3rem;
        list-style: none;
        .link {
          text-decoration: none;
          color: white;
        }
      }
    }
  }
}

// Mobile
@media (min-width: $breakpoint-m-min) and (max-width: $breakpoint-m-max) {
  .navbar {
    margin-top: 1em;
    color: white;
    ul {
      display: flex;
      justify-content: space-between;

      li {
        margin-right: 2rem;
        list-style: none;
        .link {
          text-decoration: none;
          color: white;
        }
      }
    }
  }
}
